#modal_contact_us .contact_us__close {
  width: 20px;
  height: 30px;
  display: block;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  right: 30px;
  top: 30px;
  transition: 0.2s all;
}

#modal_contact_us .contact_us__close::before,
#modal_contact_us .contact_us__close::after {
  content: "";
  display: block;
  width: 22px;
  height: 2px;
  background-color: #000000;
  position: absolute;
  left: 50%;
  top: 50%;
}

#modal_contact_us .contact_us__close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

#modal_contact_us .contact_us__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

