/*------- responsive CSS -------*/
@media (min-width: 1681px) {
	.container {
		max-width: 1480px;
		padding: 0;
	}
	#sidemenu .sidemenu_main {
		padding: 0;
	}
	#sidemenu ul.sidemenu_nav {
		padding-left: 0;
	}
	.basic_paddings {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

@media (max-width: 1680px) {
	.container {
		padding: 0 48px;
	}
}

@media (max-width: 1440px) {
	.house .agent {
		margin-bottom: 20px;
	}
}

@media (max-width: 1300px) {
	.properties {
		padding-top: 60px;
		padding-bottom: 60px;
	}
	.property-map {
		padding-top: 48px;
	}
	.basic_paddings {
		padding-top: 60px;
		padding-bottom: 60px;
	}
}

@media (min-width: 1212px) {
	#modal_contact_us .second_container {
		width: auto;
		min-width: 606px;
	}
}
@media (max-width: 1200px) {
	#navbar .header .nav_hide {
		display: none;
	}
	.col3 .property_img {
		margin-bottom: 20px;
		height: 350px;
	}
}
@media (min-width: 1025px) {
	#modal_contact_us .first_container,
	#modal_contact_us .second_container {
		padding: 0 30px;
	}
	#modal_contact_us .contact_us__container {
		padding-top: 48px;
		padding-bottom: 48px;
	}
}

@media (max-width: 1024px) {
	.col3 .property {
		width: 50%;
	}
	.features_amenities__list strong {
		font-size: 15px;
	}
	.vertical_paddings {
		padding-top: 64px;
		padding-bottom: 64px;
	}
	#modal_contact_us .contact_us__container {
		flex-direction: column-reverse;
	}
	#modal_contact_us .first_container,
	#modal_contact_us .second_container {
		max-width: 100%;
		flex-basis: 100%;
	}
	#modal_contact_us h2.mob {
		display: block;
	}
	#modal_contact_us h2.desk {
		display: none;
	}
} // max 1024

@media (max-width: 1023px) {
	.work_with_us__content {
		max-width: 100%;
		// width: 100%;
	}
}

@media (max-width: 992px) {
	.properties_wrapper .property_img {
		margin-bottom: 20px;
		height: 350px;
	}
	.properties_wrapper .property {
		margin-bottom: 85px;
	}
	.properties_wrapper .property_header {
		flex-direction: column;
		margin-bottom: 20px;
	}
	.properties_wrapper .property_header h3 {
		max-width: 100%;
		margin-bottom: 5px;
		line-height: 1.33;
		font-size: 24px;
	}
	.property_header .properties_grid_price {
		font-size: 16px;
		line-height: 1.5;
		margin-bottom: 5px;
	}
	.properties_wrapper .property_address {
		max-width: 100%;
		font-size: 15px;
		margin-bottom: 5px;
	}
	.properties_wrapper .property_details {
		margin-bottom: 12px;
		line-height: 1.64;
		font-size: 14px;
	}
	.properties_wrapper .properties_grid_footer .link {
		font-size: 14px;
		margin-right: 15px;
	}
	.house .agent {
		margin-bottom: 0;
	}
	.features_amenities__list li {
		flex-direction: column;
		margin-bottom: 10px;
	}
	.section_content__row {
		display: flex;
		flex-direction: column;
	}
	.section_content__col.text,
	.reverse .section_content__col.text {
		padding: 0;
		margin-top: 29px;
		margin-bottom: 0;
	}
	.section_content__row.straight {
		flex-direction: column;
	}
	#section-49ed873c-337f-4c0d-b905-3b838bfaa35f .hoverable-image__text-col {
		padding-left: 0;
		margin-top: 29px;
		margin-bottom: 0;
	}
}

@media (max-width: 991px) {
	.property .big_img .scroll {
		display: none;
	}
}

@media (max-width: 960px) {
	#sidemenu .sidemenu_main {
		padding-right: 40px;
	}
	#sidemenu ul.sidemenu_nav {
		padding-left: 32px;
	}
	.house .col {
		max-width: none;
		flex-basis: 100%;
		margin-bottom: 30px;
	}
	.house .info {
		font-size: 15px;
	}
	.house .title {
		margin-bottom: 9px;
	}
	.house {
		padding-top: 55px;
		padding-bottom: 55px;
	}
}

@media (max-width: 900px) {
	.properties_wrapper .property {
		width: 100%;
	}
	.footer .footer_details {
		grid-template-columns: 1fr max-content;
	}
	.footer .footer_description {
		margin-top: -27px;
	}
}

@media (min-width: 961px) {
	.mobile-contacts {
		display: none;
	}
	.house .row::after {
		content: "";
		position: absolute;
		right: 0;
		top: 0;
		background-image: radial-gradient(black 8%, transparent 9%);
		background-size: 43px 44px;
		background-position: center;
		background-repeat: repeat;
		width: 96px;
		height: 96px;
		z-index: 1;
		transform: translate(0, -50%);
	}
	.features_amenities__container::after {
		content: "";
		position: absolute;
		right: 0;
		bottom: 0;
		background-image: radial-gradient(black 8%, transparent 9%);
		background-size: 43px 44px;
		background-position: center;
		background-repeat: repeat;
		width: 96px;
		height: 96px;
		z-index: 1;
		transform: translate(5%, -12%);
	}
}

@media (max-width: 960px) {
	.container {
		padding: 0 32px;
	}
	#homepage_intro .homepage_opening__greeting {
		font-size: 15px;
	}
	#homepage_intro .homepage_opening__main {
		margin-top: 120px;
	}
}

@media (max-width: 900px) {
	#homepage_intro .video {
		display: none;
	}
	#homepage_intro .poster {
		display: block;
	}
	#homepage_intro .homepage_opening__main {
		padding: 0;
	}
}

@media (max-width: 768px) {
	.properties {
		padding-top: 48px;
		padding-bottom: 48px;
	}
	#sidemenu .sidemenu_nav .link {
		font-size: 20px;
	}
	.property .big_img img {
		height: 600px;
	}
	.features_amenities {
		padding-top: 60px;
		padding-bottom: 60px;
	}
	.feature_list__wrapper {
		flex-wrap: wrap;
	}
	#modal_contact_us .agent {
		flex-direction: column;
	}
	#modal_contact_us .agent li {
		margin-right: 0;
	}
}

@media (max-width: 800px) {
	.feature_content:not(:nth-child(2)) {
		border-right-width: 0px;
		border-right-style: initial;
		border-right-color: initial;
	}
}

@media screen and (max-width: 768px) {
	.header_logo img {
		max-width: 150px;
	}
	.work_with_us__content {
		padding: 48px 32px;
	}
	.footer .footer_bar {
		flex-direction: column;
		align-items: flex-start;
	}
	.footer .footer_bar .socials .socials_link {
		margin: 25px 32px 15px 0;
	}
	.footer .footer_bar .socials li {
		padding-right: 10px;
	}
	.footer .footer_bar .socials {
		flex-wrap: wrap;
	}
	.house .feature {
		max-width: 50%;
		flex-basis: 50%;
	}
	.property-map__wrap {
		padding-bottom: 80%;
	}
	.property-map {
		padding-top: 60px;
	}
	.feature_content:nth-child(2) {
		border-right-width: 0px;
		border-right-style: initial;
		border-right-color: initial;
	}
	.content_row {
		flex-wrap: wrap;
	}
	.services__wrapper {
		font-size: 16px;
	}
	.company_stats__col {
		max-width: 100%;
		flex-basis: 100%;
		width: 100%;
	}
	.stat_card__value {
		min-height: auto;
	}
	#modal_contact_us .contact_us__map {
		height: 291px;
	}
} // max 768

@media screen and (max-width: 767px) {
	#bannar .bannar_scrolldown {
		width: 34px;
		height: 34px;
	}
	.work_with_us h2,
	.work_with_us .text_content {
		margin: 0 auto 24px;
	}
	.footer .footer_details {
		grid-template-columns: 1fr;
	}
	.footer .footer_details .title {
		margin-top: 12px;
	}
	.footer_details .company p {
		margin-bottom: 0;
	}
	.footer .footer_description {
		margin-top: 0;
	}
	#sidemenu .sidemenu_main {
		padding-left: 40px;
	}
	.features_amenities .feature {
		width: 100%;
		margin-bottom: 0;
		border-bottom: none;
	}
	.features_amenities .feature:last-child {
		border-bottom: solid 1px #eeeeee;
	}
	.features_amenities__list li {
		flex-direction: row;
		margin-bottom: 0;
	}
	.video .container,
	.video .container.reverse {
		flex-direction: column;
	}
	.video .content {
		width: 100%;
	}
	.video__title {
		margin-bottom: 4px;
	}
	.video .content p {
		max-width: 100%;
		margin: 0 0 17px;
	}
	.video .poster {
		width: 100%;
		margin-top: 60px;
	}
	.video .poster_container {
		padding: 20% 0;
	}
	.company_stats__row {
		margin-top: 16px;
	}
	.slider__container,
	#slider_testimonials2 .slider__container {
		padding: 35px 30px;
	}
	.slider__item-content {
		font-size: 20px;
		margin: 21px 0;
	}
	.slider__item-author {
		font-size: 15px;
	}
	#slider_testimonials2 .slider__item .slider__item-content {
		margin-left: 20px;
	}
	.slider__item img {
		margin: 20px 0;
	}
	.video_testimonial .socials {
		justify-content: space-around;
	}
	.video_testimonial .socials li {
		padding-right: 0;
	}
	.video_testimonial .socials .socials_link {
		margin: 0 auto;
	}
} // max 767

@media (max-width: 650px) {
	.properties_wrapper .property_img {
		height: 225px;
	}
	.properties_wrapper .property_link {
		width: 100%;
	}

	.house .request_info .btn {
		margin: 30px 0 10px 0;
		display: block;
	}
	.house .agents_wrap {
		margin-bottom: 46px;
	}
	.house .agent .agent_name {
		font-size: 15px;
	}
	.house .share > span {
		font-size: 15px;
	}
	.house .feature {
		max-width: 100%;
		flex-basis: 100%;
	}
	.house .feature_value {
		font-size: 20px;
		line-height: 1.5;
	}
	.house .feature_title {
		font-size: 15px;
		line-height: 1.53;
	}
	#modal_contact_us .submit {
