@media (max-height: 650px) {
	.property .big_img .scroll {
		display: none;
	}
} // max height 650

@media (max-width: 580px) {
	.header .tel_item .tel {
		display: none;
	}
}
@media screen and (min-width: 576px) {
	.btn {
