// ------------ footer -------------------
.footer.container {
	padding-top: 48px;
	padding-bottom: 48px;
	font-size: 14px;
	line-height: 24px;
	background-color: inherit;
	color: inherit;
	overflow: hidden;
}
footer {
	color: #3e4247;
}
.footer a {
	color: inherit;
}
.footer h6 {
	font-family: inherit;
}
.footer .logo {
	width: 250px;
	max-width: 250px;
	max-height: 75px;
	margin-bottom: 24px;
}
.footer .logo img {
	width: 100%;
}
.footer .footer_details {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	gap: 30px 50px;
	line-height: 30px;
}
.footer .footer_details .title {
	margin-bottom: 10px;
	min-height: 23px;
}
// footer no-line
.footer_details .link.no-line:hover {
	text-decoration: underline;
}
//
.footer .link:not(.no-line) {
	position: relative;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
	color: #303339;
	background: linear-gradient(currentColor, currentColor) 0 calc(100% + 1px)
		no-repeat;
	background-size: 0 1px;
	transition: background-size 0.3s;
}
.footer .link:not(.no-line):hover {
	background-size: 100% 1px;
}

.footer_details .subscribe {
	max-width: 300px;
}
.footer .label_group > input {
	height: 40px;
	padding-top: 11px;
	padding-bottom: 11px;
}
.footer .submit {
	min-width: 42px;
	// padding: 5px;
	border: 1px solid #000;
	background-color: #000;
	color: #fff;
	font-size: inherit;
	line-height: 1;
	text-align: center;
	vertical-align: middle;
	cursor: pointer;
	transition: all 0.25s;
}
.footer .submit:hover {
	background-color: #fff;
	border-color: #a7a7a7;
	color: #000;
}

.footer .footer_description {
	padding-right: 40px;
	padding-top: 45px;
}
.footer .footer_description p {
	margin: 0;
}
.footer .footer_description img {
	width: 100px;
	margin-bottom: 32px;
	margin-top: 20px;
}
.footer_details .address address {
	font-style: normal;
}

.footer .footer_bar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 15px;
	margin-bottom: 10px;
}
.footer .footer_bar .dev_by {
	font-size: 14px;
}
.footer .footer_bar .socials {
	display: flex;
	margin: 0;
	padding: 0;
}

.footer .footer_bar .socials .socials_link {
	margin: 0 10px 0 24px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 5px;
	border: solid 1px #eeeeee;
	border-radius: 50%;
	width: 36px;
	height: 36px;
	flex-shrink: 0;
	font-size: 15px;
	transition: all 0.2s;
	background-color: #fff;
	color: #000;
	cursor: pointer;
}
.footer_bar .socials .socials_link svg,
i {
	max-width: 100%;
	transition: all 0.2s;
	fill: currentColor;
}
.footer .footer_bar .socials .socials_link:hover {
	border-color: rgba(0, 0, 0, 1);
	background-color: rgba(0, 0, 0, 1);
	color: rgba(255, 255, 255, 1);
}
.footer .footer_copyright span {
	padding: 0 8px;
}

// responsive
@media (max-width: 1200px) {
	.footer .footer_details {
		grid-template-columns: 1fr 1fr 1fr;
		gap: 30px 20px;
	}
}
