.sold.custom_intro {
	background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
		url(https://res.cloudinary.com/luxuryp/images/f_auto,q_auto/dauwytajz43tx4ylvjwu/modern-exterior-neighborhood);
}
// ----------- properties ------------------
.properties {
	padding-top: 80px;
	padding-bottom: 80px;
}
// grid flex
.properties_wrapper {
	display: flex;
	flex-wrap: wrap;
	margin-top: 30px;
}
// grid flex item(wrapper)
.properties_wrapper .property {
	width: 50%;
	margin-bottom: 106px;
	// display: flex;
}
// grid flex item(wrapper)
.col3 .property {
	width: 33.3%;
	margin-bottom: 106px;
	// display: flex;
}
// a
.properties_wrapper .property_link {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
}
.col3 .property_link {
	width: 95%;
}
.properties_wrapper .property_link:hover {
	.property_img img {
		transform: scale(1.05);
	}
}
.properties_wrapper .property_img {
	margin-bottom: 30px;
	// font-size: 0;
	height: 422px;
	background: #eee;
	overflow: hidden;
	position: relative;
}
.properties_wrapper .status {
	position: absolute;
	top: 0;
	right: 0;
	z-index: 100;
	background: #000000;
	color: #ffffff;
	font-size: 16px;
	text-transform: uppercase;
	padding: 3px 10px;
}
.property_img img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	transition: 0.3s linear;
}
.properties_wrapper .property_header {
	display: flex;
	justify-content: space-between;
}
.property_header h3 {
	margin-top: 0;
	margin-bottom: 10px;
	max-width: 441px;
	margin-right: 20px;
	line-height: 1.3;
}
.property_header .properties_grid_price {
	font-size: 20px;
	line-height: 1.3;
	font-family: "CompassSerif", serif;
}
.properties_wrapper .property_address {
	margin-bottom: 10px;
	line-height: 1.33;
	text-transform: none;
	letter-spacing: 1.2;
	font-size: 18px;
}
.properties_wrapper .property_details {
	margin-bottom: 18px;
	line-height: 1.5;
}
.properties_wrapper .properties_grid_footer .link {
	position: relative;
	font-size: 14px;
	margin-right: 15px;
	transition: background-size 0.3s;
	color: #303339;
	background: linear-gradient(currentColor, currentColor) 0 calc(100% + 1px)
		no-repeat;
	background-size: 0 1px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.properties_wrapper .properties_grid_footer .link:hover {
	background-size: 100% 1px;
}

// pagination style
.pagination_container {
	position: static;
}
.pagination_container .pagination {
	font: inherit;
	box-sizing: border-box;
}
.pagination_container .pagination ul {
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
}
.pagination_container .pagination li {
	list-style-type: none;
	margin: 0 2px;
}
.pagination_container .pagination li.pagination_prev a,
.pagination_container .pagination li.pagination_next a {
	border-color: transparent !important;
	background-color: transparent !important;
	font-size: 0;
	font-weight: bold;
}
.pagination_container .pagination li.disabled a {
	pointer-events: none;
	opacity: 0.5;
}
.pagination_container .pagination li > a {
	position: relative;
	display: block;
	padding: 5px;
	border-radius: 0;
	min-width: auto;
	height: auto;
	background: transparent;
	font-size: 12px;
	line-height: 1;
	letter-spacing: 2px;
	text-align: center;
	cursor: pointer;
	transition: all 0.3s;
	font-weight: 400;
	color: #757575;
}
.pagination_container .pagination li.pagination_prev a:before {
	content: "⟵" !important;
	font-size: 12px;
}
.pagination_container .pagination li.pagination_next a:before {
	content: "⟶" !important;
	font-size: 12px;
}
.pagination_container .pagination li.active a {
	// height: auto;
	// line-height: 1;
	// background: transparent;
	color: #303339;
}
.pagination_container .pagination li[data-num="1"] a:before,
.pagination_container .pagination li[data-num="2"] a:before {
	content: "0";
}
.pagination_container .pagination li > a:hover {
	background: transparent;
	text-decoration: underline;
}

// image section
.image_section .work_with_us {
	background-image: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)),
		url("https://res.cloudinary.com/luxuryp/images/f_auto,q_auto/ebvfwwozoxeu9zyzsnyw/lwhn47olucqmyw90");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}
.work_with_us h2,
.work_with_us .text_content {
	margin: 0 0 32px;
	max-width: 540px;
	color: #ffffff;
}
.work_with_us__content {
	max-width: 424px;
	text-align: center;
}
.image_section .work_with_us {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	position: relative;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.work_with_us .text_content {
	white-space: pre-line;
}
