.property .big_img {
	background: #000
		url("https://d1e1jt2fj4r8r.cloudfront.net/uploads/compass/bg-lines-1c.svg")
		no-repeat top center;
	background-size: cover;
	position: relative;
}
.property .big_img img {
	width: 100%;
	position: relative;
	left: 0px;
	top: 0px;
	height: 100vh;
	object-fit: cover;
}
.property .big_img .scroll {
	cursor: pointer;
	border: 2px solid #ffffff;
	display: block;
	width: 34px;
	height: 34px;
	transition: 0.3s linear;
	position: absolute;
	bottom: 48px;
	right: 48px;
	font-size: 12px;
}

.house {
	padding-top: 213px;
	padding-bottom: 84px;
}
.house .row {
	margin-left: -20px;
	margin-right: -20px;
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	position: relative;
}
.house .col {
	width: 100%;
	max-width: 50%;
	flex-basis: 50%;
	margin-bottom: 75px;
	padding-left: 20px;
	padding-right: 20px;
}
.house .photo {
	width: 100%;
	height: 419px;
}
.house .photo img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.house .text_block {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}
.house .agent_container {
	margin-bottom: 26px;
}
.house .agent {
	display: flex;
}
.house .agent .agent_name {
	margin: auto 0;
}
.house .agent_img {
	width: 60px;
	height: 60px;
	margin-right: 18px;
	border-radius: 50%;
	background: center no-repeat;
	background-size: cover;
}
.house .agent_container .agent_img {
	background-image: url("https://res.cloudinary.com/luxuryp/image/upload/w_960,c_limit,f_auto,q_auto/bDQ3U3x87jm3KJWb");
}
.house .title {
	margin-bottom: 19px;
}
.house .title h2 {
	margin: 0 0 15px 0;
}
.house .info {
	font-size: 18px;
	font-weight: 500;
}
.house .description p {
	margin-top: 0;
	margin-bottom: 1em;
}
.house .request_info {
	margin-bottom: 35px;
}
.house .request_info a {
	margin-right: 5px;
	margin-top: 20px;
}
.house .share {
	display: flex;
	align-items: center;
}
.house .share > span {
	font-size: 18px;
	font-weight: 500;
	margin-right: 15px;
}
.house .socials > li {
	margin-bottom: 0;
	width: 64px;
}
.house .socials a {
	margin: 0;
}

.house .features_list {
	display: flex;
	flex-wrap: wrap;
	margin: 0;
	padding: 0;
}
.house .feature {
	padding: 30px 35px;
	display: block;
	width: 100%;
	max-width: 33.3%;
	flex-basis: 33.3%;
	border: 1px solid #eeeeee;
	margin-right: -1px;
	margin-bottom: -1px;
}
.house .feature_value {
	font-size: 26px;
	opacity: 0.6;
	line-height: 1.31;
	margin-bottom: 10px;
}
.house .feature_title {
	line-height: 1.33;
	font-size: 18px;
	font-weight: 500;
}
.features_amenities {
	padding-top: 80px;
	padding-bottom: 80px;
}
.features_amenities h2 {
	margin-top: 0;
}
.features_amenities__container {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	position: relative;
}
.features_amenities .feature {
	border: solid 1px #eeeeee;
	width: calc(50% - 22px);
	padding: 29px 38px 34px;
	margin-bottom: 59px;
}
.features_amenities__title {
	opacity: 0.6;
	margin-top: 0;
}
.features_amenities__list li {
	line-height: 1.88;
	font-size: 16px;
	letter-spacing: 1px;
	display: flex;
	flex-wrap: wrap;
}
.features_amenities__list strong {
	font-weight: 500;
	font-size: 18px;
	line-height: 1.67;
	margin-right: 10px;
}
.property-map {
	padding-top: 80px;
}
.property-map h2 {
	text-align: center;
}
.property-map__wrap {
	padding-bottom: 40%;
	position: relative;
}
.property-map__canvas {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	overflow: hidden;
}
