}
body > .modal {
	position: fixed;
	overflow-y: auto;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.92);
	z-index: 1000;
	display: block;
}
body > .modal .modal-content {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 100%;
	min-height: 100vh;
}
body > .modal .lightbox-video {
	width: 100%;
	max-width: 177vh;
}
.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
}
.embed-container embed,
.embed-container iframe,
.embed-container object {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
}
body > .modal .close {
	display: block;
	position: absolute;
	right: 8vw;
	top: 8vh;
	width: 3.2em;
	cursor: pointer;
	color: #fff;
	font-size: 75%;
	border: none;
	background: 0 0;
	z-index: 2;
}
body > .modal .close img {
	width: 100%;
	height: 100%;
}
